import "./App.scss";
import React, { useState } from "react";
import Loading from "./Loading";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import helpers from "./helper";
const textReco = [
  "CÔNG HÒA XÃ HỘI CHỦ NGHĨA VIỆT NAM",
  "Độc lập - Tự do - Hạnh phúc",
  "CHỨNG MINH NHÂN DÂN",
  "001099001224",
  "Họ và tên khại sinh:",
  "NGUYỄN ĐÌNH THỊNH",
  "Họ và tên gọi khác:",
  "Ngày, tháng, năm 			sinh: 19/11/1999",
  "Giới tính: Nam",
  "Dân tộc: Kinh",
  "Quê quán: Nam Định",
  "Nơi thường trú: Xóm 7, Cổ 		Nhuế 1",
  "Bắc Từ Liêm, Hà Nội",
  "Có giá trị đến: 16/04/2029",
  "Đặc điểm nhân dạng: Nốt ruồi C:1.5cm 			trèn sau",
  "duôi mày phái",
  "Ngày 16 tháng 04 năm 2014",
  "CUC TRƯỞNG CUC CẢNH SÁT",
  "ĐKQL CƯ TRÚ VÀ 			DLOG VỀ DÂN CƯ",
  "Vũ Xuân Dung",
];
function App() {
  const [front, setFront] = useState();
  const [back, setBack] = useState();
  const [face, setFace] = useState();
  const [data, setData] = useState();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState();
  const renderUpload = (type) => {
    let text;
    switch (type) {
      case "front": {
        text = "Chọn ảnh mặt trước";
        break;
      }
      case "back": {
        text = "Chọn ảnh mặt sau";
        break;
      }
      case "face": {
        text = "Chọn ảnh chân dung";
        break;
      }
    }
    const onchangeFile = (event) => {
      if (event.target.files.length === 0) {
        return;
      }
      const getBase64 = (e) => {
        var file = e.target.files[0];
        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => {
          let file = reader.result;
          switch (type) {
            case "front": {
              setFront(file);
              break;
            }
            case "back": {
              setBack(file);
              break;
            }
            case "face": {
              setFace(file);
              break;
            }
          }
        };
        reader.onerror = function (error) {
          console.log("Error: ", error);
        };
      };
      getBase64(event);
    };
    return (
      <div className="upload">
        <span className="upload__text">{text}</span>
        <input
          type="file"
          className="input__upload"
          accept="image/*"
          onChange={onchangeFile}
          webkitdirectory
          directory
        ></input>
      </div>
    );
  };
  const renderResult = () => {
    if (loading) {
      return <Loading />;
    }
    if (!data) {
      return <span>Chưa có dữ liệu!</span>;
    }
    return (
      <div>
        <div className="result">
          <div className="face">
            <div className="face__image">
              <img src={data?.img_face2}></img>
              <img src={data?.img_face1}></img>
            </div>
            <div className="similarity">
              <span style={{ fontSize: "20px", fontWeight: 500, color: "red" }}>
                Độ tương đồng: {Math.round(data?.similarity * 100) + "%"}
              </span>
            </div>
          </div>
          <div className="stactics">
            <img src={data?.img_card_front}></img>
            <img src={data?.img_card_back}></img>
          </div>
        </div>
        <div className="result-text">
          <span style={{ fontWeight: 500 }}>Nội dung nhận diện:</span>
          <div className="result-text__des">
            {data?.content?.map((e) => {
              return <p>{e}</p>;
            })}
          </div>
        </div>
      </div>
    );
  };
  const onSubmit = async () => {
    setLoading(true);
    if (!front || !back || !face) {
      toast("🦄 Vui lòng chọn đầy đủ ảnh!");
      setLoading(false);
      return;
    }
    let options = {
      image_portrait: face,
      image_front: front,
      image_back: back,
    };
    let result = await helpers.getData(options);
    console.log(result);
    if (result.status === 200) {
      let data = result.data;
      setData(data);
    } else {
      setError("Không thể detect ảnh!");
    }
    setLoading(false);
  };
  const renderImage = (url, removeImage) => {
    return (
      <div className="image__render">
        <span
          className="remove"
          onClick={() => {
            removeImage("");
          }}
        >
          🦄
        </span>
        <img src={url}></img>
      </div>
    );
  };
  return (
    <div className="container">
      <div className="header">
        <h1 style={{ color: "#4dd0e1", fontSize: "50px" }}>eKYC</h1>
      </div>
      <div className="card">
        <div className="card__front card__item">
          {front ? renderImage(front, setFront) : renderUpload("front")}
        </div>
        <div className="card__back card__item">
          {back ? renderImage(back, setBack) : renderUpload("back")}
        </div>
        <div className="card__face card__item">
          {face ? renderImage(face, setFace) : renderUpload("face")}
        </div>
      </div>
      <div className="scan">
        <div className="scan__button" onClick={onSubmit}>
          Scan
        </div>
      </div>
      <div className="divider"></div>
      <div className="end-result">{renderResult()}</div>
      <ToastContainer position="top-center" />
    </div>
  );
}

export default App;
